<template>
  <div class="providersearch">
    <NavBar />

    <!-- Filters Side Bar -->
    <b-sidebar
      type="is-light"
      :fullheight="true"
      :fullwidth="false"
      :overlay="true"
      :right="false"
      v-model="open"
      id="bsidebar"
    >
      <!-- Filter by Specialty Dropdown  -->

      <br /><br />

      <b-field
        label="Filter by Specialty"
        label-position="on-border"
        style="margin-top: 70%"
      >
        <b-select placeholder="Default" v-model="specFilter">
          <option
            v-for="option in distinctSpecialty"
            :value="option"
            :key="option"
          >
            {{ option }}
          </option>
        </b-select>
      </b-field>

      <!-- Filter by Language Dropdown  -->
      <b-field label="Filter by Language" label-position="on-border">
        <b-select placeholder="Default" v-model="langFilter">
          <option
            v-for="option in distinctLanguage"
            :value="option"
            :key="option"
          >
            {{ option }}
          </option>
        </b-select>
      </b-field>

      <!-- Filter by City Dropdown  -->
      <b-field label="Filter by City" label-position="on-border">
        <b-select placeholder="Default" v-model="cityFilter">
          <option v-for="option in distinctCity" :value="option" :key="option">
            {{ option }}
          </option>
        </b-select>
      </b-field>

            <!-- Filter by State Dropdown  -->
      <b-field label="Filter by State" label-position="on-border">
        <b-select placeholder="Default" v-model="stateFilter">

          <option v-for="option in distinctState" :value="option" :key="option">
            {{ option }}
          </option>
        </b-select>
      </b-field>

      <b-button
        size="is-medium"
        icon-left="filter-off-outline"
        @click="clearFilters"
        type="is-primary"
        style="margin-top: 200%"
        >Clear Filters</b-button
      >
    </b-sidebar>

    

    <!-- Search Bar -->

    <b-field class="bfield">
      <b-input
        placeholder="Search by Name or NPI"
        type="search"
        class="search"
        size="is-medium"
        icon="magnify"
        v-model="search"
        icon-right="close-circle"
        icon-right-clickable
        @icon-right-click="clearSearch"
      >
      </b-input>

      <!-- Filters Button -->
      <b-button
        size="is-medium"
        icon-left="filter-minus-outline"
        @click="open = true"
        type="is-primary"
        >Filters</b-button
      >
    </b-field>

    <!-- Notification if not HTTP 200 -->
    <section v-if="errored">
      <b-notification
        type="is-danger"
        has-icon
        aria-close-label="Close notification"
        role="alert"
      >
        Unable to load content. Please try again later.
      </b-notification>
    </section>

    <section v-else>
      <!-- Loader to be displayed while getting data -->
      <div v-if="loading">
        <b-loading :is-full-page="true" v-model="loading"></b-loading>
      </div>
      <!-- Table to display providers -->
      <b-table
        v-else
        :data="filteredSearch"
        :hoverable="true"
        :paginated="true"
        :pagination-simple="false"
        :per-page="8"
        :current-page.sync="currentPage"
        :show-detail-icon="true"
        detailed
        class="datatable"
      >
        <b-table-column
          field="filteredSearch.Name"
          label="Name"
          sortable
          centered
          v-slot="props"
        >
          {{ props.row.Name }}
        </b-table-column>

        <b-table-column
          field="filteredSearch.Specialty"
          label="Specialty"
          sortable
          centered
          v-slot="props"
        >
          {{ props.row.Specialty }}
        </b-table-column>

        <b-table-column
          field="filteredSearch.NPINO"
          label="NPI Number"
          sortable
          centered
          v-slot="props"
        >
          {{ props.row.NPINO }}
        </b-table-column>

        <template #detail="props">
          <div class="tile is-ancestor">
            <div class="subtitle is-6 tile is-child box">
              Address: {{ props.row.Address }} {{ props.row.City }},
              {{ props.row.State }}, {{ props.row.Zip }}<br />
              County: {{ props.row.County }}<br />
              Phone: {{ props.row.Phone }}<br />
              Language: {{ props.row.Language }}<br />
            </div>

            <div
              class="tile is-child box container">

            
            
      <iframe
      class="responsive-iframe" 
        width="600" 
        height="350" 
        frameborder="0" 
        style="border:0"
        loading="lazy" 
        allowfullscreen 
        :src="
              'https://www.google.com/maps/embed/v1/place?q=' +
                props.row.Address + 
                '+' + 
                props.row.City +
                '+' +
                props.row.State +
                '+' +
                props.row.Zip + 
                '&key='+key">
      </iframe>
      
            <!--
              <b-button
                icon-left="map-marker-radius-outline"
                type="is-primary"
                size="is-large"
                rounded
                tag="a"
                :href="
                  'https://www.google.com/maps/place/' +
                  props.row.Address +
                  '+' +
                  props.row.City +
                  '+' +
                  props.row.State +
                  '+' +
                  props.row.Zip
                "
                target="_blank"
                >Map Directions
              </b-button>
              -->
            </div>
          </div>
        </template>
      </b-table>

      <!-- Notification if search does return results -->
      <section v-if="filteredSearch.length < 1">
        <b-message type="is-danger" has-icon class="nomatch">
          No matches. Please search again.
        </b-message>
      </section>
    </section>

    <br /><br />

        <b-message type="is-danger" has-icon class="disclaimer">
            Imperial Health makes every effort to deliver up-to-date provider information on our website, but data is subject to change at any time. Please contact us directly for additional assistance.
        </b-message>

    <Footer />
  </div>
</template>

<style scoped>
.search {
  margin: auto;
  width: 50%;
  padding-bottom: 3%;
}

.bfield {
  padding-top: 3em;
  margin-right: auto;
  width: 80%;
}

.datatable {
  margin: auto;
  max-width: 90%;
  padding-bottom: 5%;
}

.nomatch {
  margin-bottom: 3em;
}

.b-sidebar .sidebar-content.is-light {
  width: 500px !important;
}

.disclaimer {
  height: 20%;
  width: 60%;
  margin-bottom: 10em !important;
  margin: auto;
}

.container {
  position: relative;
  width: 100%;
  overflow: hidden;
  padding-top: 10em;
}

.responsive-iframe {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  width: 100%;
  height: 100%;
  border: none;
}
</style>

<script>
// @ is an alias to /src
import NavBar from "@/components/NavBar.vue";
import Footer from "@/components/Footer.vue";
import axios from "axios";

export default {
  name: "ProviderSearch",
  components: {
    NavBar,
    Footer,
  },
  data() {
    return {
      info: [],
      loading: true,
      errored: false,
      open: false,
      search: "",
      currentPage: 1,
      specFilter: "",
      langFilter: "",
      cityFilter: "",
      stateFilter: "",
      key: process.env.VUE_APP_KEY
    };
  },
  methods: {
    clearSearch() {
      this.search = "";
    },

    clearFilters() {
      this.specFilter = "";
      this.langFilter = "";
      this.cityFilter = "";
      this.stateFilter = "";
    },
  },

  mounted() {
    axios
      .get("https://imperialhealthdev.github.io/providers.json")
      .then((response) => {
        this.info = response.data.data;
      })
      .catch((error) => {
        console.log(error);
        this.errored = true;
      })
      .finally(() => (this.loading = false));
  },

  computed: {
    //Pass search query and filter json by Name or NPINO
    filteredSearch: function () {
      return this.info.filter((x) => {
        return (
          (x.Name.toLowerCase().match(this.search.toLowerCase()) ||
            x.NPINO.toLowerCase().match(this.search.toLowerCase())) &&
          x.Specialty.match(this.specFilter) &&
          x.Language.match(this.langFilter) &&
          x.City.match(this.cityFilter) &&
          x.State.match(this.stateFilter)
          
        );
      });
    },

    //Function to filter specialty field so no dupes are in dropdown options
    distinctSpecialty: function () {
      return this.info
        .map((x) => x.Specialty)
        .filter((v, i, s) => s.indexOf(v) === i)
        .sort();
    },

    distinctLanguage: function () {
      return this.info
        .map((x) => x.Language)
        .filter((v, i, s) => s.indexOf(v) === i)
        .sort();
    },

    distinctCity: function () {
      return this.info
        .map((x) => x.City)
        .filter((v, i, s) => s.indexOf(v) === i)
        .sort();
    },

      distinctState: function () {
      return this.info
        .map((x) => x.State)
        .filter((v, i, s) => s.indexOf(v) === i)
        .sort();
    },
  },
};
</script>
